.topnav {
    overflow: hidden;
    // position: relative;

  }

  .topnav, #nav-links, #socials, #search {
    display: none;
  }



  .topnav a.icon {
    display: block;
    // position: absolute;
    right: 0;
    top: 0;
  }

  .head{

      display: flex;
      justify-content: space-between;
      padding: 5px;

  }


#nav-links{

  ul{

    text-align: center;
    padding: 0;
    margin: 0;
  }

}

  .nav-li{

    list-style-type: none;
    background-color: $lightBlue;
    padding: 10px;
    border: 1px solid $darkBlue;
  }

 .nav-a{

  text-decoration: none;
  color: white;
  cursor: pointer;
  padding: 10px 50px;

 }

 .social-links{

  background-color: white;
  text-align: center;
  border: 1px solid $lightBlue;

  .fa{

    text-decoration: none;
    color: $lightBlue;
    font-size: 2em;
    padding: 5px;
  

  }
 }

 .nav-a:hover{

  color: $lightBlue;

 }

 .nav-li:hover{

  background-color: #fff;
 }

 .fa:hover{
  color: $lightGrey;
  transition: .5s ease;
 }

 #search{

  position: relative;

  input{

    border: none;
    border-radius: 5%;
    background-color:$lightGrey;
    padding: 10px;
    width: 100%;
  }

  #search-btn{

    position: absolute;
   right: 3%;
   top: 15%;

  }
 }

 

 .top-nav, .btn-main-nav{

  @media (min-width:$tabletSmall){

    display: none;
  }
 }



 #dsk-search{

  display: none;

  @media (min-width: $tabletSmall){
    display: block;

  position: relative;

  input{

    border: none;
    border-radius: 5%;
    background-color:$lightGrey;
    padding: 10px;
    width: 100%;
  }

  #search-img{
    position: absolute;
   right: 3%;
   top: 15%;

  }

 }
}



.desktop-nav{

  display: none;

  @media (min-width: $tabletSmall){

    display: block;

    #dsk-search{

      display: block;
    }

    .desk-nav-li{

      list-style-type: none;
    }

    .desk-nav-a{

      text-decoration: none;
      color: white;

    }

    .desk-nav{

      background-color: $lightBlue;
      padding: 1px;

    }

    .ul-desk-nav{

      margin: 5px;
      padding: 0;
      display: flex;
      justify-content: space-evenly;
    }

    .desk-nav-li:hover{

      background-color: white;

    }

    .desk-nav-a:hover{


      color: $lightBlue;
      padding: 15px;
      

    }
  }
}


.desk-social-links{

  display: none;

  @media (min-width: $tabletLarge){

    display: block;

    .fa{

      text-decoration: none;
      color: $lightBlue;
      font-size: 1.5em;
      padding: 5px;
    
    }

    .fa:hover{
      color: $lightGrey;
      transition: .5s ease;
     }
  }
}
